import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validationRosok from "../../Validations/validationRosok";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";

class FormKodeRosok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      listPerhiasan: [],
      listJual: [],
      listBeli: [],
      listKategori: [],
    };
  }

  submit = () => {
    console.log(JSON.stringify(this.state));
  };

  componentDidMount() {
    AxiosMasterGet("kadar/get/by-type/kadar-cetak")
      .then((res) => {
        this.setState({
          listPerhiasan: res.data,
        });
      })
      .catch((err) => console.log(err));
    AxiosMasterGet("kadar/get/by-type/kadar-beli")
      .then((res) => {
        this.setState({
          listBeli: res.data,
        });
      })
      .catch((err) => console.log(err));
    AxiosMasterGet("kadar/get/by-type/kadar-jual")
      .then((res) => {
        this.setState({
          listJual: res.data,
        });
      })
      .catch((err) => console.log(err));
    AxiosMasterGet("kategori/get/all")
      .then((res) => {
        this.setState({
          listKategori: res.data.filter(
            (list) =>
              list.kode_kategori === "CIOK" || list.kode_kategori === "RONGSOK"
          ),
        });
        console.log(res.data);
      })
      .catch((err) => console.log(err));
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          {/* Awal FIeld */}
          <div className="col-md-3">
            <div className="form-group">
              <Field
                component={renderFieldSelect}
                label="Kode Kategori"
                name="kodeKategori"
                value={this.props.status}
                normalize={(value) => value && value.toUpperCase()}
                className="form-control"
                options={this.state.listKategori.map((list) => {
                  let data = {
                    value: `${list.kode_kategori}||${list.kode_group}`,
                    name: list.nama_kategori,
                  };
                  return data;
                })}
              ></Field>
            </div>
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-3">
            <Field
              name="kodeJenis"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Jenis"
            />
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-6">
            <Field
              name="namaJenis"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Nama Jenis"
            />
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-3">
            <div className="form-group">
              <Field
                component={renderFieldSelect}
                label="Kadar Cetak"
                name="kadarCetak"
                value="DEFAULT"
                className="form-control"
                options={this.state.listPerhiasan.map((obj) => {
                  var payload = {
                    value: obj.kadar,
                    name: obj.kadar,
                  };
                  return payload;
                })}
              ></Field>
            </div>
          </div>
          {/* AKhir FIeld */}
          {/* Awal Field */}
          <div className="col-md-3">
            <div className="form-group">
              <Field
                component={renderFieldSelect}
                label="Kadar Beli"
                name="kadarBeli"
                value="DEFAULT"
                className="form-control"
                options={this.state.listBeli.map((obj) => {
                  var payload = {
                    value: obj.kadar,
                    name: obj.kadar,
                  };
                  return payload;
                })}
              ></Field>
            </div>
          </div>
          {/* AKhir FIeld */}
          {/* Awal Field */}
          <div className="col-md-3">
            <div className="form-group">
              <Field
                component={renderFieldSelect}
                label="Kadar Jual"
                name="kadarJual"
                value="DEFAULT"
                className="form-control"
                options={this.state.listJual.map((obj) => {
                  var payload = {
                    value: obj.kadar,
                    name: obj.kadar,
                  };
                  return payload;
                })}
              ></Field>
            </div>
          </div>
          {/* AKhir FIeld */}

          <div className="col-lg-12 col-md-12 mt-3">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Submit
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKodeRosok = reduxForm({
  form: "Rosok",
  enableReinitialize: true,
  validate: validationRosok,
})(FormKodeRosok);
export default connect()(FormKodeRosok);
